import React from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import axios from 'axios';

const donationSchema = z.object({
  amount: z.number().min(1, 'Сума має бути більше 0'),
  shelterId: z.string().min(1, 'Оберіть притулок'),
  message: z.string().optional(),
  paymentMethod: z.enum(['card', 'paypal'], {
    required_error: 'Оберіть спосіб оплати'
  })
});

type DonationFormData = z.infer<typeof donationSchema>;

interface DonationFormProps {
  shelterId?: string;
  onSuccess?: () => void;
}

const DonationForm: React.FC<DonationFormProps> = ({ shelterId, onSuccess }) => {
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting }
  } = useForm<DonationFormData>({
    resolver: zodResolver(donationSchema),
    defaultValues: {
      shelterId: shelterId || ''
    }
  });

  const onSubmit = async (data: DonationFormData) => {
    try {
      await axios.post('http://localhost:3000/donations', data);
      onSuccess?.();
    } catch (error) {
      console.error('Помилка при відправці пожертвування:', error);
    }
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
      <div>
        <label htmlFor="amount" className="block text-sm font-medium text-gray-700">
          Сума пожертвування
        </label>
        <div className="mt-1">
          <input
            type="number"
            id="amount"
            {...register('amount', { valueAsNumber: true })}
            className="input"
          />
        </div>
        {errors.amount && (
          <p className="mt-1 text-sm text-red-600">{errors.amount.message}</p>
        )}
      </div>

      {!shelterId && (
        <div>
          <label htmlFor="shelterId" className="block text-sm font-medium text-gray-700">
            Оберіть притулок
          </label>
          <div className="mt-1">
            <select
              id="shelterId"
              {...register('shelterId')}
              className="input"
            >
              <option value="">Оберіть притулок</option>
              {/* Тут буде список притулків */}
            </select>
          </div>
          {errors.shelterId && (
            <p className="mt-1 text-sm text-red-600">{errors.shelterId.message}</p>
          )}
        </div>
      )}

      <div>
        <label htmlFor="message" className="block text-sm font-medium text-gray-700">
          Повідомлення (необов'язково)
        </label>
        <div className="mt-1">
          <textarea
            id="message"
            {...register('message')}
            rows={3}
            className="input"
          />
        </div>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">
          Спосіб оплати
        </label>
        <div className="mt-2 space-y-2">
          <div className="flex items-center">
            <input
              type="radio"
              id="card"
              value="card"
              {...register('paymentMethod')}
              className="h-4 w-4 text-purple-600 focus:ring-purple-500 border-gray-300"
            />
            <label htmlFor="card" className="ml-3 block text-sm font-medium text-gray-700">
              Кредитна картка
            </label>
          </div>
          <div className="flex items-center">
            <input
              type="radio"
              id="paypal"
              value="paypal"
              {...register('paymentMethod')}
              className="h-4 w-4 text-purple-600 focus:ring-purple-500 border-gray-300"
            />
            <label htmlFor="paypal" className="ml-3 block text-sm font-medium text-gray-700">
              PayPal
            </label>
          </div>
        </div>
        {errors.paymentMethod && (
          <p className="mt-1 text-sm text-red-600">{errors.paymentMethod.message}</p>
        )}
      </div>

      <button
        type="submit"
        disabled={isSubmitting}
        className="w-full btn btn-primary"
      >
        {isSubmitting ? 'Обробка...' : 'Зробити пожертвування'}
      </button>
    </form>
  );
};

export default DonationForm; 