import { createSlice, PayloadAction } from '@reduxjs/toolkit';

interface Pet {
  id: string;
  name: string;
  type: string;
  breed: string;
  age: number;
  description: string;
  imageUrl: string;
  shelterId: string;
}

interface PetsState {
  pets: Pet[];
  featuredPets: Pet[];
  selectedPet: Pet | null;
  isLoading: boolean;
  error: string | null;
}

const initialState: PetsState = {
  pets: [],
  featuredPets: [],
  selectedPet: null,
  isLoading: false,
  error: null,
};

const petsSlice = createSlice({
  name: 'pets',
  initialState,
  reducers: {
    fetchPetsStart: (state) => {
      state.isLoading = true;
      state.error = null;
    },
    fetchPetsSuccess: (state, action: PayloadAction<Pet[]>) => {
      state.isLoading = false;
      state.pets = action.payload;
    },
    fetchPetsFailure: (state, action: PayloadAction<string>) => {
      state.isLoading = false;
      state.error = action.payload;
    },
    fetchFeaturedPetsStart: (state) => {
      state.isLoading = true;
      state.error = null;
    },
    fetchFeaturedPetsSuccess: (state, action: PayloadAction<Pet[]>) => {
      state.isLoading = false;
      state.featuredPets = action.payload;
    },
    fetchFeaturedPetsFailure: (state, action: PayloadAction<string>) => {
      state.isLoading = false;
      state.error = action.payload;
    },
    setSelectedPet: (state, action: PayloadAction<Pet | null>) => {
      state.selectedPet = action.payload;
    },
    clearError: (state) => {
      state.error = null;
    },
  },
});

export const {
  fetchPetsStart,
  fetchPetsSuccess,
  fetchPetsFailure,
  fetchFeaturedPetsStart,
  fetchFeaturedPetsSuccess,
  fetchFeaturedPetsFailure,
  setSelectedPet,
  clearError,
} = petsSlice.actions;

export default petsSlice.reducer; 