import { createSlice, PayloadAction } from '@reduxjs/toolkit';

interface Shelter {
  id: string;
  name: string;
  address: string;
  phone: string;
  email: string;
  description: string;
  imageUrl: string;
  capacity: number;
  currentPets: number;
}

interface SheltersState {
  shelters: Shelter[];
  selectedShelter: Shelter | null;
  isLoading: boolean;
  error: string | null;
}

const initialState: SheltersState = {
  shelters: [],
  selectedShelter: null,
  isLoading: false,
  error: null,
};

const sheltersSlice = createSlice({
  name: 'shelters',
  initialState,
  reducers: {
    fetchSheltersStart: (state) => {
      state.isLoading = true;
      state.error = null;
    },
    fetchSheltersSuccess: (state, action: PayloadAction<Shelter[]>) => {
      state.isLoading = false;
      state.shelters = action.payload;
    },
    fetchSheltersFailure: (state, action: PayloadAction<string>) => {
      state.isLoading = false;
      state.error = action.payload;
    },
    setSelectedShelter: (state, action: PayloadAction<Shelter | null>) => {
      state.selectedShelter = action.payload;
    },
    updateShelterCapacity: (
      state,
      action: PayloadAction<{ shelterId: string; currentPets: number }>
    ) => {
      const shelter = state.shelters.find((s) => s.id === action.payload.shelterId);
      if (shelter) {
        shelter.currentPets = action.payload.currentPets;
      }
      if (state.selectedShelter?.id === action.payload.shelterId) {
        state.selectedShelter.currentPets = action.payload.currentPets;
      }
    },
    clearError: (state) => {
      state.error = null;
    },
  },
});

export const {
  fetchSheltersStart,
  fetchSheltersSuccess,
  fetchSheltersFailure,
  setSelectedShelter,
  updateShelterCapacity,
  clearError,
} = sheltersSlice.actions;

export default sheltersSlice.reducer; 